html {
  font-size: 62.5%;
}

body {
  background-color: #181818;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.4rem;
  color: #fff;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

/* Blocks */
.page {
  overflow: hidden;
}

.header {
  width: calc(100% - 300px);
  height: 30px;
  background-color: #444;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
}

.sidebar {
  display: none;
  width: 300px;
  height: 100vh;
  background-color: #ccc;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.main {
  padding: 10rem 0 8rem 300px;
}

.container {
  max-width: 70rem;
  margin: 0 auto;
  padding-left: 5rem;
  padding-right: 5rem;
}

.footer {
  width: calc(100% - 300px);
  height: 30px;
  background-color: #444;
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 1000;
}